import { Injectable } from '@angular/core';
import { PrintFunctionsOfFiltervalue } from './cloudprint.function.filtervalue';
import { PrintFunctionsOfFormat } from './cloudprint.function.format';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { PrintFunctionsOfConvert } from './cloudprint.function.convert';


// Ã¥Â¼ÂÃ¥ÂÂ¥StimulsoftÃ§ÂÂjsÃ¥ÂºÂ
declare var Stimulsoft;

@Injectable()
export class CloudprintFunctionRegister {

  private httpoptions = {};

  constructor(private http: HttpClient) { 
    this.httpoptions['headers'] = {
      SessionId:'',
      'Content-Type':'application/json'
    };
  }

  /**
   * Ã¥ÂÂÃ¥Â§ÂÃ¥ÂÂÃ¨ÂÂªÃ¥Â®ÂÃ¤Â¹ÂÃ¥ÂÂ½Ã¦ÂÂ°
   */
  public initCustomFunction(configUrl) {

    // Ã¥ÂÂÃ§Â½Â®Ã¥ÂÂ½Ã¦ÂÂ°-Ã¦ÂÂ°Ã¥ÂÂ¼Ã¨Â¿ÂÃ¦Â»Â¤
    const funcs = new PrintFunctionsOfFiltervalue();
    // Ã¥ÂÂÃ§Â½Â®Ã¥ÂÂ½Ã¦ÂÂ°-Ã¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂ
    const funcFormat = new PrintFunctionsOfFormat();
    // Ã¥ÂÂÃ§Â½Â®Ã¥ÂÂ½Ã¦ÂÂ°-Ã¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂ
    const funcConvert = new PrintFunctionsOfConvert();

    // Ã¨Â¿ÂÃ©ÂÂÃ©Â»ÂÃ¨Â®Â¤Ã§ÂÂGSPÃ¥ÂÂÃ§Â½Â®Ã¥ÂÂ½Ã¦ÂÂ°Ã¥ÂÂÃ§Â»ÂÃ¤Â¸ÂºGSP
    if (Stimulsoft.Report.Dictionary.StiFunctions.getFunctions('IGixFucs').length === 0) {
        // tslint:disable-next-line:forin
        for (const f in funcs) {
            const func = funcs[f];
            Stimulsoft.Report.Dictionary.StiFunctions.addFunction(
              func.Category,
              func.GroupFunctionName,
              func.FunctionName,
              func.Description,
              func.TypeOfFunction,
              func.ReturnType,
              func.ReturnDescription,
              func.ArgumentTypes,
              func.ArgumentNames,
              func.ArgumentDescriptions,
              func.Function
              );
        }

        // tslint:disable-next-line:forin
        for (const f in funcFormat) {
          const func = funcFormat[f];
          Stimulsoft.Report.Dictionary.StiFunctions.addFunction(
            func.Category,
            func.GroupFunctionName,
            func.FunctionName,
            func.Description,
            func.TypeOfFunction,
            func.ReturnType,
            func.ReturnDescription,
            func.ArgumentTypes,
            func.ArgumentNames,
            func.ArgumentDescriptions,
            func.Function);
        }

        // tslint:disable-next-line:forin
        for (const f in funcConvert) {
          const func = funcConvert[f];
          Stimulsoft.Report.Dictionary.StiFunctions.addFunction(
            func.Category,
            func.GroupFunctionName,
            func.FunctionName,
            func.Description,
            func.TypeOfFunction,
            func.ReturnType,
            func.ReturnDescription,
            func.ArgumentTypes,
            func.ArgumentNames,
            func.ArgumentDescriptions,
            func.Function);
        }
    }


    // Ã¨ÂÂªÃ¥Â®ÂÃ¤Â¹ÂÃ¥ÂÂ½Ã¦ÂÂ°todo:Ã¦ÂÂÃ§Â»ÂÃ¨ÂÂªÃ¥Â®ÂÃ¤Â¹ÂÃ¥ÂÂ½Ã¦ÂÂ°Ã¤Â»ÂÃ¥ÂÂÃ¥ÂÂ°Ã¥ÂÂÃ©ÂÂÃ§Â½Â®Ã¦ÂÂÃ¤Â»Â¶Ã©ÂÂÃ§ÂÂÃ¨Â§Â£Ã¦ÂÂ
    return this.http.get(configUrl,this.httpoptions).toPromise()
    .then((data:[]) => {
      if(data && data.length>0){
        return this.addJsFuncPlugins(-1, data)
        .then(() => {
          if (!data) {
            return;
          }
          const allFuncs = [];
          // tslint:disable-next-line:forin
          if(data.length>0){
            for (const i in data) {
              allFuncs.push(window[(data[i] as any).key]);
            }
          }

  
          
          if (Stimulsoft.Report.Dictionary.StiFunctions.getFunctions('CustomFucs').length === 0 && allFuncs.length>0) {
            // tslint:disable-next-line:forin
            for (const f in allFuncs) {
                const item = allFuncs[f];
                // tslint:disable-next-line:forin
                for (const f2 in item) {
                  const func = item[f2];
                  Stimulsoft.Report.Dictionary.StiFunctions.addFunction(
                    'CustomFucs',
                    func.GroupFunctionName,
                    func.FunctionName,
                    func.Description,
                    func.TypeOfFunction,
                    func.ReturnType,
                    func.ReturnDescription,
                    func.ArgumentTypes,
                    func.ArgumentNames,
                    func.ArgumentDescriptions,
                    func.Function);
              }
            }
          }
        });
      }

    });
  }

  /**
   * Ã¦Â·Â»Ã¥ÂÂ jsÃ¤Â¸ÂÃ¥ÂÂ¡Ã¥ÂÂ½Ã¦ÂÂ°Ã¦ÂÂÃ¤Â»Â¶
   */
  private addJsFuncPlugins(i, data) {
    if (!data && data.length==0) {
      const sub = new Subject<any>();
      sub.complete(); // Ã¦Â²Â¡Ã¦ÂÂÃ¨Â¿ÂÃ¥ÂÂ¥Ã¨Â¯ÂtoPromise()Ã©ÂÂÃ§ÂÂÃ¥ÂÂÃ¥Â®Â¹Ã¤Â¸ÂÃ¨Â·Â
      return sub.toPromise();
    }

    const loader = window['System'];
    if (i < data.length - 1) {
      i++;
      this.addJsFuncPlugins(i, data);
    }
    return loader.import(data[i].path);
  }

}
