/**
 * Ã©ÂÂÃ¤Â»Â¶Ã¦ÂÂÃ¥ÂÂ°Ã§ÂÂÃ¦ÂÂÃ¥ÂÂ¡
 */
@Injectable()
export class CloudPrintAttachmentService {
 
  /**
   * Ã¦ÂÂÃ¥ÂÂ°Ã¥ÂÂÃ¦ÂÂ°Ã¥Â®ÂÃ¤Â½Â
   */
  printEntity = new PrintEntity();

  /**
   * Ã¨Â®Â¾Ã§Â½Â®PrintEntityÃ¥ÂÂÃ¦ÂÂ°
   * @description Ã¥Â¦ÂÃ¦ÂÂÃ¥Â¤ÂÃ§ÂÂnewÃ¦ÂÂ°Ã§ÂÂPrintEntityÃ¯Â¼ÂÃ¥ÂÂÃ©ÂÂÃ¨Â¦ÂÃ¨Â°ÂÃ§ÂÂ¨Ã¨Â¯Â¥Ã¦ÂÂ¹Ã¦Â³ÂÃ¥ÂÂÃ¦Â­Â¥serviceÃ§ÂÂentity
   *              Ã¥Â¤ÂÃ§ÂÂÃ¦Â²Â¡Ã¦ÂÂnewÃ¦ÂÂ°Ã§ÂÂPrintEntityÃ¯Â¼ÂÃ¥ÂÂÃ¤Â¸ÂÃ©ÂÂÃ¨Â¦ÂÃ¨Â°ÂÃ§ÂÂ¨Ã¨Â¯Â¥Ã¦ÂÂ¹Ã¦Â³Â
   */
  set setPrintEntity(entity) {
    this.printEntity = entity;
    // this.designerSrv.printEntity = this.printEntity;
    // this.viewerSrv.printEntity = this.printEntity;
    this.formatSrv.printEntity = this.printEntity;
  }

  localeid:string;
  localpipe: LocalLangPipe;

  constructor(
    private formatSrv: PrintFormatService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
    private applicationRef: ApplicationRef,
    private notifyService: NotifyService,
    private appService: AppService,
    private modalService: BsModalService,
    private httpClient: HttpClient
    ) {
        // Ã¨Â®Â¾Ã§Â½Â®printEntityÃ§ÂÂÃ©Â»ÂÃ¨Â®Â¤Ã¥ÂÂ¼
        this.printEntity.appUrl = Urls.CloudPrintBaseUrl;
        this.printEntity.isUseMetaData = true;
        this.printEntity.language = localStorage.getItem("languageCode") || 'zh-CHS';
    
        this.formatSrv.printEntity = this.printEntity;


        if(this.injector)
        {
          this.modalService = this.injector.get(BsModalService);
          this.localeid = localStorage.getItem("languageCode") || this.injector.get(LOCALE_ID);
          this.localpipe = new LocalLangPipe(this.localeid);
        } 
    }


    async printAttachment(attachmentoptions: AttachmentOptions, options: OutputOptions,su=null,param=null) {
      if(DataValidator.checkAppUrl(this.printEntity.appUrl)==false){
        this.notifyService.info(this.localpipe.transform('appurlerror'));
        return;
      }
      if(attachmentoptions.queryType==undefined){//Ã¥ÂÂ¼Ã¥Â®Â¹Ã¤Â»Â¥Ã¥ÂÂÃ§ÂÂÃ©ÂÂ»Ã¨Â¾Â
        attachmentoptions.queryType = QueryType.Unknown;
      }

      if(options.printType==undefined){//Ã¥ÂÂ¼Ã¥Â®Â¹Ã¤Â»Â¥Ã¥ÂÂÃ§ÂÂÃ©ÂÂ»Ã¨Â¾ÂÃ¨Â¿ÂÃ©ÂÂÃ©Â»ÂÃ¨Â®Â¤Ã¤Â¸ÂºÃ¨Â¡Â¨Ã¥ÂÂÃ¦ÂÂÃ¥ÂÂ°
        options.printType = PrintType.Attachment;
      }

      const that = this;
      return this.formatSrv.getPrintDevice().then((data)=>{
        const device = data || [];
        const factory = that.resolver.resolveComponentFactory(FormatlistjobComponent);
        const printsetting = factory.create(that.injector);
  
        printsetting.instance.boxlist = device;
        printsetting.instance.formatId = '';
        printsetting.instance.isShowFormat = false;
        printsetting.instance.printType = 1;
        printsetting.instance.localepip = that.localpipe;

        const dlg = this.modalService.show(printsetting,{
          title: this.localpipe.transform('printset'),
          width: 800, 
          height: 500,
          showButtons: true,
          showMaxButton:false,
          buttons: [
            { text: this.localpipe.transform('print'), cls: 'btn btn-primary', handle: () => {
              printsetting.instance.printJob();
            }},
            { text: this.localpipe.transform('cancel'), cls: 'btn btn-secondary', handle: () => {
              dlg.close();
            }}
          ],
        });  
        //Ã¨Â®Â¢Ã©ÂÂÃ¤ÂºÂÃ¥Â¼Â¹Ã¥ÂÂºÃ§ÂÂÃ¦Â Â¼Ã¥Â¼ÂÃ©ÂÂÃ¦ÂÂ©Ã¥ÂÂÃ¨Â¡Â¨Ã§ÂÂÃ¥ÂÂ³Ã©ÂÂ­Ã¤ÂºÂÃ¤Â»Â¶
        printsetting.instance.print.subscribe(async (data) => {
            dlg.close();
            options.printSetting = data.printSetting;
            options.printerName = data.printter;
            options.DeviceId = data.boxId;
            options.printJob = true;
            return await this.printAttachmentJob(attachmentoptions,options);
        });
      });

  
      
    }

    private async printAttachmentJob(attachmentoptions: AttachmentOptions, options: OutputOptions){
      const url = this.printEntity.appUrl + Urls.PrintAttachment+ 'su=' + Config.PRINT_SU;;

      const httpoptions = {};
      httpoptions['headers'] = {
          SessionId: this.printEntity.sessionId || '',
          'Content-Type':'application/json'
      };
      const data = {
        attachmentOptions:attachmentoptions,
        options:options,
      }

      return await this.httpClient.post(url,data,httpoptions).toPromise()
      .then(()=>{
        this.notifyService.info(this.localpipe.transform('sendprintjob'));
        return this.localpipe.transform('sendprintjob');
      })
      .catch((err)=>{
        this.notifyService.warning(err.error.Message);
        return err.error.Message;
      });    
    }

}
