// @dynamic
export class Utils {

    /**
     * Ã¥ÂÂ¤Ã¦ÂÂ­Ã¦ÂÂ¯Ã¥ÂÂ¦PCÃ§Â«Â¯ or Ã§Â§Â»Ã¥ÂÂ¨Ã§Â«Â¯
     * true:PC
     * false:Mobile
     */
    public static isPC(): boolean {

        if ((navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i))) {
            return false;  // Ã§Â§Â»Ã¥ÂÂ¨Ã§Â«Â¯
        } else {
            return true;
        }
    }

    public static uuid(): string {
        return [this.gen(2), this.gen(1), this.gen(1), this.gen(1), this.gen(3)].join('-');
    }

    public static renderPdfByByte(rendedFormat,ele){
        if (rendedFormat) {

          const lock = setInterval(function() {
            if (ele) { 
                const v = document.getElementById('viewframe') as any;
                if(v){
                    const filebtn = v.contentWindow.document.getElementById('openFile');
                    const printbtn =  v.contentWindow.document.getElementById('print');
                    const downbtn =  v.contentWindow.document.getElementById('download');
                    let file = false;
                    let down = false;
                    let print = false;
                    if(filebtn) {
                        filebtn.style.display='none';
                        file = true;
                    }
                    if(downbtn) {
                        downbtn.style.display='none';
                        down = true;
                    }
                    if(printbtn) {
                        printbtn.style.display='none';
                        print = true;
                    }
    
                    if(file && down && print){
                        clearInterval(lock);
                    }
                }else{
                    const binaryData = [];
                    // binaryData.push(self.convertBase64ToUint8Array(rendedFormat));
                    binaryData.push(new Uint8Array(rendedFormat));
                    this.url =window.URL.createObjectURL(new Blob(binaryData, {type:'application/pdf'}));
                    // Ã©ÂÂÃ¨Â¿ÂpdfjsÃ¦ÂÂ¥Ã©Â¢ÂÃ¨Â§Âpdf
                    ele.innerHTML = '<iframe id="viewframe" src="/platform/common/web/pdfjs/viewer.html?file='+encodeURIComponent(this.url)+'" height="100%" width="100%"></iframe>';
                    ele.hidden = false;
                }
            }
          }, 200);
        }
    }
    
    private static gen(count: number) {
        let out = '';
        for (let i = 0; i < count; i++) {
            // tslint:disable-next-line:no-bitwise
            out += (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
        }
        return out;
    }

}
