    public static ConvertCurrency(money) {
        //Ã¦Â±ÂÃ¥Â­ÂÃ§ÂÂÃ¦ÂÂ°Ã¥Â­Â
        var cnNums = new Array('Ã©ÂÂ¶', 'Ã¥Â£Â¹', 'Ã¨Â´Â°', 'Ã¥ÂÂ', 'Ã¨ÂÂ', 'Ã¤Â¼Â', 'Ã©ÂÂ', 'Ã¦ÂÂ', 'Ã¦ÂÂ', 'Ã§ÂÂ');
        //Ã¥ÂÂºÃ¦ÂÂ¬Ã¥ÂÂÃ¤Â½Â
        var cnIntRadice = new Array('', 'Ã¦ÂÂ¾', 'Ã¤Â½Â°', 'Ã¤Â»Â');
        //Ã¥Â¯Â¹Ã¥ÂºÂÃ¦ÂÂ´Ã¦ÂÂ°Ã©ÂÂ¨Ã¥ÂÂÃ¦ÂÂ©Ã¥Â±ÂÃ¥ÂÂÃ¤Â½Â
        var cnIntUnits = new Array('', 'Ã¤Â¸Â', 'Ã¤ÂºÂ¿', 'Ã¥ÂÂ');
        //Ã¥Â¯Â¹Ã¥ÂºÂÃ¥Â°ÂÃ¦ÂÂ°Ã©ÂÂ¨Ã¥ÂÂÃ¥ÂÂÃ¤Â½Â
        var cnDecUnits = new Array('Ã¨Â§Â', 'Ã¥ÂÂ', 'Ã¦Â¯Â«', 'Ã¥ÂÂ');
        //Ã¦ÂÂ´Ã¦ÂÂ°Ã©ÂÂÃ©Â¢ÂÃ¦ÂÂ¶Ã¥ÂÂÃ©ÂÂ¢Ã¨Â·ÂÃ§ÂÂÃ¥Â­ÂÃ§Â¬Â¦
        var cnInteger = 'Ã¦ÂÂ´';
        //Ã¦ÂÂ´Ã¥ÂÂÃ¥Â®ÂÃ¤Â»Â¥Ã¥ÂÂÃ§ÂÂÃ¥ÂÂÃ¤Â½Â
        var cnIntLast = 'Ã¥ÂÂ';
        //Ã¦ÂÂÃ¥Â¤Â§Ã¥Â¤ÂÃ§ÂÂÃ§ÂÂÃ¦ÂÂ°Ã¥Â­Â
        var maxNum = 999999999999999.9999;
        //Ã©ÂÂÃ©Â¢ÂÃ¦ÂÂ´Ã¦ÂÂ°Ã©ÂÂ¨Ã¥ÂÂ
        var integerNum;
        //Ã©ÂÂÃ©Â¢ÂÃ¥Â°ÂÃ¦ÂÂ°Ã©ÂÂ¨Ã¥ÂÂ
        var decimalNum;
        //Ã¨Â¾ÂÃ¥ÂÂºÃ§ÂÂÃ¤Â¸Â­Ã¦ÂÂÃ©ÂÂÃ©Â¢ÂÃ¥Â­ÂÃ§Â¬Â¦Ã¤Â¸Â²
        var chineseStr = '';
        //Ã¥ÂÂÃ§Â¦Â»Ã©ÂÂÃ©Â¢ÂÃ¥ÂÂÃ§ÂÂ¨Ã§ÂÂÃ¦ÂÂ°Ã§Â»ÂÃ¯Â¼ÂÃ©Â¢ÂÃ¥Â®ÂÃ¤Â¹Â
        var parts;
        if (money == '') { return ''; }
        money = parseFloat(money);
        if (money >= maxNum) {
          //Ã¨Â¶ÂÃ¥ÂÂºÃ¦ÂÂÃ¥Â¤Â§Ã¥Â¤ÂÃ§ÂÂÃ¦ÂÂ°Ã¥Â­Â
          return '';
        }
        if (money == 0) {
          chineseStr = cnNums[0] + cnIntLast + cnInteger;
          return chineseStr;
        }
        //Ã¨Â½Â¬Ã¦ÂÂ¢Ã¤Â¸ÂºÃ¥Â­ÂÃ§Â¬Â¦Ã¤Â¸Â²
        money = money.toString();
        if (money.indexOf('.') == -1) {
          integerNum = money;
          decimalNum = '';
        } else {
          parts = money.split('.');
          integerNum = parts[0];
          decimalNum = parts[1].substr(0, 4);
        }
        //Ã¨ÂÂ·Ã¥ÂÂÃ¦ÂÂ´Ã¥ÂÂÃ©ÂÂ¨Ã¥ÂÂÃ¨Â½Â¬Ã¦ÂÂ¢
        if (parseInt(integerNum, 10) > 0) {
          var zeroCount = 0;
          var IntLen = integerNum.length;
          for (var i = 0; i < IntLen; i++) {
            var n = integerNum.substr(i, 1);
            var p = IntLen - i - 1;
            var q = p / 4;
            var m = p % 4;
            if (n == '0') {
              zeroCount++;
            } else {
              if (zeroCount > 0) {
                chineseStr += cnNums[0];
              }
              //Ã¥Â½ÂÃ©ÂÂ¶
              zeroCount = 0;
              chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
            }
            if (m == 0 && zeroCount < 4) {
              chineseStr += cnIntUnits[q];
            }
          }
          chineseStr += cnIntLast;
        }
        //Ã¥Â°ÂÃ¦ÂÂ°Ã©ÂÂ¨Ã¥ÂÂ
        if (decimalNum != '') {
          var decLen = decimalNum.length;
          //Ã¥Â¤ÂÃ§ÂÂÃ¥ÂÂ°Ã¥ÂÂ
          if (decLen > 2) 
          {
              decLen = 2;
          }
          for (var i = 0; i < decLen; i++) {
            var n = decimalNum.substr(i, 1);
            if (n != '0') {
              chineseStr += cnNums[Number(n)] + cnDecUnits[i];
            }
          }
        }
        if (chineseStr == '') {
          chineseStr += cnNums[0] + cnIntLast + cnInteger;
        } else if (decimalNum == '') {
          chineseStr += cnInteger;
        }
        return chineseStr;
    }

