    /**
    * Ã¥Â¯Â¹Ã¦ÂÂ¥Ã¦ÂÂÃ¨Â¿ÂÃ¨Â¡ÂÃ¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ¯Â¼Â Ã¥ÂÂC#Ã¥Â¤Â§Ã¨ÂÂ´Ã¤Â¸ÂÃ¨ÂÂ´ Ã©Â»ÂÃ¨Â®Â¤yyyy-MM-dd HH:mm:ss
    * Ã¥ÂÂ¯Ã¤Â¸ÂÃ¥Â¸Â¦Ã¥ÂÂÃ¦ÂÂ° Ã¤Â¸ÂÃ¤Â¸ÂªÃ¦ÂÂ¥Ã¦ÂÂÃ¥ÂÂÃ¦ÂÂ° Ã¦ÂÂÃ¤Â¸ÂÃ¤Â¸ÂªÃ¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ¥ÂÂÃ¦ÂÂ°
    * @param date Ã¨Â¦ÂÃ¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ§ÂÂÃ¦ÂÂ¥Ã¦ÂÂ 
    * @param format Ã¨Â¿ÂÃ¨Â¡ÂÃ¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ§ÂÂÃ¦Â¨Â¡Ã¥Â¼ÂÃ¥Â­ÂÃ§Â¬Â¦Ã¤Â¸Â²
    *     Ã¦ÂÂ¯Ã¦ÂÂÃ§ÂÂÃ¦Â¨Â¡Ã¥Â¼ÂÃ¥Â­ÂÃ¦Â¯ÂÃ¦ÂÂÃ¯Â¼Â 
    *     y:Ã¥Â¹Â´, 
    *     M:Ã¥Â¹Â´Ã¤Â¸Â­Ã§ÂÂÃ¦ÂÂÃ¤Â»Â½(1-12), 
    *     d:Ã¦ÂÂÃ¤Â»Â½Ã¤Â¸Â­Ã§ÂÂÃ¥Â¤Â©(1-31), 
    *     H:Ã¥Â°ÂÃ¦ÂÂ¶(0-23), 
    *     h:Ã¥Â°ÂÃ¦ÂÂ¶(0-11), 
    *     m:Ã¥ÂÂ(0-59), 
    *     s:Ã§Â§Â(0-59), 
    *     f:Ã¦Â¯Â«Ã§Â§Â(0-999),
    *     q:Ã¥Â­Â£Ã¥ÂºÂ¦(1-4)
    * @return String
    * @author adswads@gmail.com
    */
    public static dateFormat(date?: any, format?: string): string {
        // Ã¦ÂÂ Ã¥ÂÂÃ¦ÂÂ°
        if (date === undefined && format === undefined) {
            date = new Date();
            format = 'yyyy-MM-dd HH:mm:ss';
        } else if (typeof (date) === 'string') {// Ã¦ÂÂ Ã¦ÂÂ¥Ã¦ÂÂ
            format = date;
            date = new Date();
        } else if (format === undefined) {// Ã¦ÂÂ Ã¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ¥ÂÂÃ¦ÂÂ°
            format = 'yyyy-MM-dd HH:mm:ss';
        } else { }// Ã¦Â²Â¡Ã¦ÂÂÃ¥ÂÂÃ©ÂÂÃ§Â¬Â¦Ã§ÂÂÃ§ÂÂ¹Ã¦Â®ÂÃ¥Â¤ÂÃ§ÂÂ

        const map = {
            y: date.getFullYear() + '', // Ã¥Â¹Â´Ã¤Â»Â½
            M: date.getMonth() + 1 + '', // Ã¦ÂÂÃ¤Â»Â½
            d: date.getDate() + '', // Ã¦ÂÂ¥
            H: date.getHours(), // Ã¥Â°ÂÃ¦ÂÂ¶ 24
            m: date.getMinutes() + '', // Ã¥ÂÂ
            s: date.getSeconds() + '', // Ã§Â§Â
            q: Math.floor((date.getMonth() + 3) / 3) + '', // Ã¥Â­Â£Ã¥ÂºÂ¦
            f: date.getMilliseconds() + '' // Ã¦Â¯Â«Ã§Â§Â
        };
        // Ã¥Â°ÂÃ¦ÂÂ¶ 12
        if (map['H'] > 12) { map['h'] = map['H'] - 12 + ''; } else { map['h'] = map['H'] + ''; }
        map['H'] += '';

        const reg = 'yMdHhmsqf';
        let all = '', str = '';
        for (let i = 0, n = 0; i < reg.length; i++) {
            n = format.indexOf(reg[i]);
            if (n < 0) { continue; }
            all = '';
            for (; n < format.length; n++) {
                if (format[n] !== reg[i]) {
                    break;
                }
                all += reg[i];
            }
            if (all.length > 0) {
                if (all.length === map[reg[i]].length) {
                    str = map[reg[i]];
                } else if (all.length > map[reg[i]].length) {
                    if (reg[i] === 'f') {
                        str = map[reg[i]] + this.charString('0', all.length - map[reg[i]].length);
                    }  else {
                        str = this.charString('0', all.length - map[reg[i]].length) + map[reg[i]];
                    }
                } else {
                    switch (reg[i]) {
                        case 'y': str = map[reg[i]].substr(map[reg[i]].length - all.length); break;
                        case 'f': str = map[reg[i]].substr(0, all.length); break;
                        default: str = map[reg[i]]; break;
                    }
                }
                format = format.replace(all, str);
            }
        }
        return format;
    }

    /**
    * Ã¨Â¿ÂÃ¥ÂÂÃ¥Â­ÂÃ§Â¬Â¦Ã¤Â¸Â² Ã¤Â¸ÂºnÃ¤Â¸ÂªcharÃ¦ÂÂÃ¦ÂÂ
    * @param char Ã©ÂÂÃ¥Â¤ÂÃ§ÂÂÃ¥Â­ÂÃ§Â¬Â¦
    * @param count Ã¦Â¬Â¡Ã¦ÂÂ°
    * @return String
    * @author adswads@gmail.com
    */
   private static charString(char: string, count: number): string {
        let str = '';
        while (count--) {
            str += char;
        }
        return str;
    }



    /**
     * Ã¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ¦ÂÂ°Ã¥Â­Â
     * @param  number  [Ã¨Â¦ÂÃ¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ§ÂÂÃ¦ÂÂ°Ã¥Â­Â]
     * @param  decimals         [Ã¤Â¿ÂÃ§ÂÂÃ¥ÂÂ Ã¤Â½ÂÃ¥Â°ÂÃ¦ÂÂ°]
     * @param  dec_point        [Ã¥Â°ÂÃ¦ÂÂ°Ã§ÂÂ¹Ã§Â¬Â¦Ã¥ÂÂ·]
     * @param  thousands_sep    [Ã¥ÂÂÃ¥ÂÂÃ¤Â½ÂÃ§Â¬Â¦Ã¥ÂÂ·]
     * @param  roundtag         [Ã¨ÂÂÃ¥ÂÂ¥Ã¥ÂÂÃ¦ÂÂ°Ã¯Â¼ÂÃ©Â»ÂÃ¨Â®Â¤ "ceil" Ã¥ÂÂÃ¤Â¸ÂÃ¥ÂÂ,"floor"Ã¥ÂÂÃ¤Â¸ÂÃ¥ÂÂ,"round" Ã¥ÂÂÃ¨ÂÂÃ¤ÂºÂÃ¥ÂÂ¥]
     * @return  Ã¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ¥ÂÂÃ§ÂÂÃ§Â»ÂÃ¦ÂÂ
     */
    private static  number_format(number, decimals, dec_point, thousands_sep, roundtag) {
        number = (number + '').replace(/[^0-9+-Ee.]/g, '');
        roundtag = roundtag || 'ceil'; // "ceil","floor","round"
        let n = !isFinite(+number) ? 0 : +number,
            prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
            sep = (typeof thousands_sep === 'undefined') ? ',' : thousands_sep,
            dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
            s = [],
            toFixedFix = function (n, prec) {

                const k = Math.pow(10, prec);
                console.log();
    
                return '' + parseFloat(Math[roundtag](parseFloat((n * k).toFixed(prec * 2))).toFixed(prec * 2)) / k;
            };
        s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.');
        const re = /(-?\d+)(\d{3})/;
        while (re.test(s[0])) {
            s[0] = s[0].replace(re, '$1' + sep + '$2');
        }
    
        if ((s[1] || '').length < prec) {
            s[1] = s[1] || '';
            s[1] += new Array(prec - s[1].length + 1).join('0');
        }
        return s.join(dec);
    }

