  /**
   * Ã¥ÂÂÃ¥Â»ÂºblobÃ¥Â¯Â¹Ã¨Â±Â¡Ã¯Â¼ÂÃ¥Â¹Â¶Ã¥ÂÂ©Ã§ÂÂ¨Ã¦ÂµÂÃ¨Â§ÂÃ¥ÂÂ¨Ã¦ÂÂÃ¥Â¼ÂurlÃ¨Â¿ÂÃ¨Â¡ÂÃ¤Â¸ÂÃ¨Â½Â½
   * @param data Ã¦ÂÂÃ¤Â»Â¶Ã¦ÂµÂÃ¦ÂÂ°Ã¦ÂÂ®
   */
  downloadFile(data) {
  
    const link = document.createElement('a');
    const blob = new Blob([data], {type: 'application/pdf;charset=UTF-8'});
    link.setAttribute('href', window.URL.createObjectURL(blob));
    link.setAttribute('download', this.printEntity.downLoadFileName+'.pdf');
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
