  private renderPdf(rendedFormat){
    const self = this;

    if (rendedFormat) {//Ã¦ÂÂÃ¦Â¸Â²Ã¦ÂÂÃ§ÂÂÃ§Â»ÂÃ¦ÂÂÃ¥ÂÂÃ§ÂÂ´Ã¦ÂÂ¥Ã¥Â±ÂÃ§Â¤Âº
      const ele = document.getElementById('viewer');
      // ele.hidden = true;
      Utils.renderPdfByByte(rendedFormat,ele);

    } else {//Ã¦Â²Â¡Ã¦ÂÂÃ¦Â¸Â²Ã¦ÂÂÃ§ÂÂÃ§Â»ÂÃ¦ÂÂÃ¥ÂÂÃ©ÂÂÃ¨Â¿ÂÃ¥ÂÂÃ§Â«Â¯Ã¦Â¸Â²Ã¦ÂÂÃ¥ÂÂÃ¥Â±ÂÃ§Â¤Âº
      this.outputParam.prnOutputOps.printJob = false;
      if (this.outputParam.prnSourceOptions.length > 1) {
        if (this.outputParam.isFilter) {
          return this.formatSrv.printMultiByFilter(this.outputParam.prnSourceOptions as Array<SourceFilterOptions>, this.outputParam.prnOutputOps, null,'CloudPrint')
          .then((data)=>{
            const b = new Blob([data]);
            const reader = new FileReader();
            reader.readAsArrayBuffer(b);
            reader.onload = function (e) {
              self.renderPdf(reader.result as any);
            };
          });
        } else {
          return this.formatSrv.printMultiById(this.outputParam.prnSourceOptions as Array<SourceOptions>, this.outputParam.prnOutputOps, null,'CloudPrint')
          .then((data)=>{
            const b = new Blob([data]);
            const reader = new FileReader();
            reader.readAsArrayBuffer(b);
            reader.onload = function (e) {
              self.renderPdf(reader.result as any);
            };
          });
        }
      } else {
        if (this.outputParam.isFilter) {
          return this.formatSrv.printSingleByFilter(this.outputParam.prnSourceOptions[0] as SourceFilterOptions, this.outputParam.prnOutputOps, null,'CloudPrint')
          .then((data)=>{
            const b = new Blob([data]);
            const reader = new FileReader();
            reader.readAsArrayBuffer(b);
            reader.onload = function (e) {
              self.renderPdf(reader.result as any);
            };
          });
        } else {
          return this.formatSrv.printSingleById(this.outputParam.prnSourceOptions[0] as SourceOptions, this.outputParam.prnOutputOps, null,'CloudPrint')
          .then((data)=>{
            const b = new Blob([data]);
            const reader = new FileReader();
            reader.readAsArrayBuffer(b);
            reader.onload = function (e) {
              self.renderPdf(reader.result as any);
            };
          });
        }
      }
    }
  }

  /** Ã§ÂÂ´Ã¦ÂÂ¥Ã¦ÂÂÃ¥ÂÂ° */
  printDirect(){
    const ele = document.getElementById('viewer');
    const iframe = ele.firstChild as any;
    iframe.contentWindow.print();

    const sourceopslist = this.outputParam.prnSourceOptions;
    let su=[];
    let id=[];
    let billcate=[];
    let biztype=[];
    let source=[];
    let entityfilter=[];
    let dataid=[];
    let isfilter=[];
    sourceopslist.forEach(x=>{
      su.push(x.ServiceUnit);
      id.push(x.FormatId);
      source.push(x.SourceId);
      isfilter.push(this.outputParam.isFilter);
      if(this.outputParam.isFilter){
        entityfilter.push(JSON.stringify((x as SourceFilterOptions).EntityFilter));
      }else{
        dataid.push((x as SourceOptions).DataIds);
      }
      billcate.push(x.billCategoryId);
      biztype.push(x.bizTypeId);
    });
    const data = {
      formatid:id,
      sourceid:source,
      entityfilter:entityfilter,
      dataid:dataid,
      isfilter:isfilter,
      su:su,
      billcategory:billcate,
      biztype:biztype
    };
    this.formatSrv.sendPrintEvent(data,"CloudPrint");
  }

  /** Ã§ÂÂÃ¥Â­ÂÃ¥ÂÂÃ¥ÂÂ°Ã¦ÂÂÃ¥ÂÂ° */
  printBox(){
    const self = this;
    self.outputParam.prnOutputOps.printJob = true;
    if (self.outputParam.prnSourceOptions.length > 1) {
      if (self.outputParam.isFilter) {
        return self.printSrv.printMultiByFilter4Job(self.outputParam.prnSourceOptions as Array<SourceFilterOptions>, self.outputParam.prnOutputOps);
      } else {
        return self.printSrv.printMultiById4Job(self.outputParam.prnSourceOptions as Array<SourceOptions>, self.outputParam.prnOutputOps);
      }
    } else {
      if (self.outputParam.isFilter) {
        return self.printSrv.printSingleByFilter4Job(self.outputParam.prnSourceOptions[0] as SourceFilterOptions, self.outputParam.prnOutputOps);
      } else {
        return self.printSrv.printSingleById4Job(self.outputParam.prnSourceOptions[0] as SourceOptions, self.outputParam.prnOutputOps);
      }
    }
  }
