import { Component, OnInit,AfterViewInit, Input, EventEmitter, ViewChild, LOCALE_ID, Inject } from '@angular/core';
import { PrintFormat } from '../entitys/printFormat';
import { DialogComponent } from '@farris/ui-dialog';
import { PrintSetting } from '../entitys/outputoptions/printsetting';
import { PrintOritation } from '../entitys/enums/printOritation';
import { PaperKind } from '../entitys/outputoptions/paperkind';
import { BsModalRef } from '@farris/ui-modal';
import { NotifyService } from '@farris/ui-notify';
import { LocalLangPipe } from '../local.pipe';

@Component({
  selector: 'lib-formatlistjob',
  templateUrl: './formatlistjob.component.html',
  styleUrls: ['./formatlistjob.component.css']
})
export class FormatlistjobComponent implements OnInit {
  // @ViewChild('printjob') printjob: DialogComponent;

  @Input() public formats : PrintFormat[];
  @Input() public boxlist : any[];

  print: EventEmitter<any> = new EventEmitter<any>();

  // dlg:BsModalRef;
  paperlist = [];
  layoutlist = [];
  columns = [];
  //Ã¦ÂÂ¯Ã¥ÂÂ¦Ã¥Â±ÂÃ§Â¤ÂºÃ¦Â Â¼Ã¥Â¼ÂÃ¥ÂÂÃ¨Â¡Â¨
  isShowFormat = true;
  //Ã¦ÂÂÃ¥ÂÂ°Ã§Â±Â»Ã¥ÂÂ0:Ã¨Â¡Â¨Ã¥ÂÂÃ¦ÂÂÃ¥ÂÂ°Ã¯Â¼Â1:Ã©ÂÂÃ¤Â»Â¶Ã¦ÂÂÃ¥ÂÂ°
  printType = 0;
  height=0;//Ã©Â»ÂÃ¨Â®Â¤Ã¤Â¸Âº0Ã¯Â¼ÂÃ¦Â Â¹Ã¦ÂÂ®Ã§ÂºÂ¸Ã¥Â¼Â Ã¥Â¤Â§Ã¥Â°ÂÃ¦ÂÂ¥
  width=0;//Ã©Â»ÂÃ¨Â®Â¤Ã¤Â¸Âº0,Ã¦Â Â¹Ã¦ÂÂ®Ã§ÂºÂ¸Ã¥Â¼Â Ã¥Â¤Â§Ã¥Â°ÂÃ¦ÂÂ¥
  top=0;
  bottom=0;
  left=0;
  right=0;
  copy=1;
  paperkind='A4';
  layout='Portrait';
  printter:string;
  printboxid='';
  formatId:string;
  //Ã¨ÂÂªÃ¥Â®ÂÃ¤Â¹ÂÃ§ÂºÂ¸Ã¥Â¼Â 
  isCustomPaper=false;
  //Ã¨ÂÂªÃ¥Â®ÂÃ¤Â¹ÂÃ©Â¡ÂµÃ¨Â¾Â¹Ã¨Â·Â
  isCustomMargin=false;

  custompaper='Ã¨ÂÂªÃ¥Â®ÂÃ¤Â¹ÂÃ§ÂºÂ¸Ã¥Â¼Â :';
  custommargin='Ã¨ÂÂªÃ¥Â®ÂÃ¤Â¹ÂÃ©Â¡ÂµÃ¨Â¾Â¹Ã¨Â·Â:';

  localepip : LocalLangPipe;
  constructor(private notifyService: NotifyService,
    @Inject(LOCALE_ID) localeid:string) { 
      
      debugger;
      // localeid = localStorage.getItem("languageCode") || localeid;
      // this.localepip = new LocalLangPipe(localeid);
      
  }

  ngOnInit() {
    debugger;
    
    this.custompaper = this.localepip.transform('custompaper');
    this.custommargin = this.localepip.transform('custommargin');

    this.columns = [
      {field: 'code', title: this.localepip.transform('formatcode')},
      {field: 'name', title: this.localepip.transform('formatname')},
    ];
    //Ã§ÂºÂ¸Ã¥Â¼Â 
    this.paperlist = PaperKind;
    //Ã¥Â¸ÂÃ¥Â±Â
    this.layoutlist = [
      {
        key:'Portrait',
        value:this.localepip.transform('portrait')
      },
      {
        key:'Landscape',
        value:this.localepip.transform('landscape')
      }
    ];
    
  }
  
  printerSelectChange(rowdata: any){
    this.printboxid = rowdata.data.boxId;
  }

  selectChange(rowdata: any){
    this.formatId = rowdata.id;
  }

  printJob(){

    if(this.checkBeforePrint()){
      const ps = new PrintSetting();
      ps.bottom = this.bottom;
      ps.top = this.top;
      ps.left = this.left;
      ps.right = this.right;
      ps.height = this.height;
      ps.width = this.width;
      ps.copys = this.copy;
      ps.paperName = this.paperkind;
      if(this.isCustomPaper){
        ps.centerPrint = false;
      }else{
        ps.centerPrint = true;
      }

      if(PrintOritation.Landscape.toString() == this.layout){
        ps.printOritation = PrintOritation.Landscape;
      }else{
        ps.printOritation = PrintOritation.Portrait;
      }
      
      this.print.emit({
        id:this.formatId,
        boxId:this.printboxid,
        printter:this.printter,
        printSetting:ps
      });  
    }
  }

  private checkBeforePrint(){
    if(!this.formatId && this.printType!=1){
      this.notifyService.warning(this.localepip.transform('selectformat'));
      return false;
    }else if(!this.printter){
      this.notifyService.warning(this.localepip.transform('selectprinter'));
      return false;
    }else if(!this.layout){
      this.notifyService.warning(this.localepip.transform('selectlayout'));
      return false;
    }else if(!this.paperkind && this.height==0 && this.width==0){
      this.notifyService.warning(this.localepip.transform('nopapersize'));
      return false;
    }else if(!this.printboxid){
      this.notifyService.warning(this.localepip.transform('noagent'));
      return false;
    }else{
      return true;
    }
  }

}
